@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background: #f8f8f8;
}

.justify-text {
  text-align: justify;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: darkcyan;
  text-decoration: none;
}

a:hover {
  color: #005e5e;
}

.section-title {
  text-align: left;
  margin: 20px 0;
}

.org-title {
  text-align: left !important;
  margin: 8px 0;
  font-size: 18px;
  font-weight: 500;
}

.text-left {
  text-align: left !important;
}

.pl-1 {
  padding-left: 15px !important;
}

td {
  vertical-align: top;
  padding: 3px 15px;
}

li {
  list-style: none;
}

b {
  font-weight: 500;
}

.card {
    background: #fff;
    border: none;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.profile-name {
    font-weight: 500;
    font-size: 36px;
}

.profile-picture {
    width: 200px;
    height: auto;
}
.social-card {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(168, 10, 10, 0.3) 0px 30px 60px -30px !important;
}
